.accbox li {
  display: block;
  background: #F4F4F4;
  border-radius: 0rem;
  margin-bottom: 1.2rem;
  position: relative;
}
.accbox li::before {
  content: "Q. ";
  font-size: 2rem;
  font-weight: 700;
  color: #0C0F1A;
  position: absolute;
  left: 1.8rem;
  top: 1.4rem;
}
.accbox .acctitle {
  font-weight: bold;
  font-size: 1.6rem;
  padding: 2rem 6rem;
  position: relative;
  cursor: pointer;
}
.accbox .acctitle::before, 
.accbox .acctitle::after {
  content: "";
  display: block;
  width: 1.6rem;
  height: 0.2rem;
  border-radius: 0rem;
  background: #0C0F1A;
  position: absolute;
  right: 2.2rem;
  top: 50%;
  transform: translateY(-50%);
}
.accbox .acctitle::after {
  transform: translateY(-50%) rotate(90deg);
  transition: 0.3s;
}
.accbox .accshow {
  display: none;
  font-size: 1.6rem;
  padding: 0 6rem 2rem;
}
.accbox .acctitle.active::after {
  transform: translateY(-50%) translate(0);
}
@media (max-width: 768px) {
  .accbox li {
    border-radius: 1rem;
    margin-bottom: 1.2rem;
  }
  .accbox li::before {
    font-size: 1.8rem;
    left: 1rem;
    top: 0.8rem;
  }
  .accbox .acctitle {
    padding: 1.2rem 3rem 1.2rem 3.5rem;
    font-size:1.4rem;
  }
  .accbox .acctitle::before, 
  .accbox .acctitle::after {
    width: 1.4rem;
    height: 0.3rem;
    right: 1rem;
  }
  .accbox .accshow {
    font-size: 1.4rem;
    padding: 0 3.3rem 1.6rem;
  }
}

